import { NavLink } from 'react-router-dom';
import { useContext } from 'react';

import {
  ACCESSORIES_LINK, HOME_LINK, PHONES_LINK, TABLETS_LINK,
} from '../../helpers/constants/Links';
import { DropDownMenuContext } from '../../helpers/context/DropDownMenuContext';

export const Nav = () => {
  const { collapseMenu } = useContext(DropDownMenuContext);

  return (
    <nav className="nav">
      <NavLink
        className="nav__link"
        to={HOME_LINK}
        onClick={collapseMenu}
      >
        home
      </NavLink>

      <NavLink
        className="nav__link"
        to={PHONES_LINK}
        onClick={collapseMenu}
      >
        phones
      </NavLink>

      <NavLink
        className="nav__link"
        to={TABLETS_LINK}
        onClick={collapseMenu}
      >
        tablets
      </NavLink>

      <NavLink
        className="nav__link"
        to={ACCESSORIES_LINK}
        onClick={collapseMenu}
      >
        accessories
      </NavLink>
    </nav>
  );
};

Nav.defaultProps = {
  vertical: false,
};
